import React, { PureComponent } from 'react';
import { Link} from 'react-router-dom';
import classNames from 'classnames';
import { withStyles } from '@material-ui/core/styles';
import Map from '../MainMap';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import withLocalstorage from '../../HOC/WithLocalStorage';
import { connect } from 'react-redux';
import { getIsOrder } from '../../modules/Order';
import {createTaxiOrder, resetTaxiOrder} from '../../modules/Order';
import Tooltip from '@material-ui/core/Tooltip';
import { getAddressList } from './api';

const CastomTooltip = withStyles(theme => ({
    tooltip: {
      backgroundColor: 'whiteSmoke',
      border: '1px solid lightGrey',
      color: 'rgba(0, 0, 0, 0.87)',
      boxShadow: theme.shadows[1],
      fontSize: 16,
    },
    }))(Tooltip);

const styles = theme => ({
    root: {
        position:'relative',
        margin:0,
        padding:0      
    },
    form_container:{
        display: 'flex',
        flexWrap: 'wrap',
        flexDirection: 'column',
        justifyContent: 'left',
        //width: '40%'
    },
    container: {
        ...theme.mixins.gutters(),
        paddingTop: theme.spacing.unit * 2, 
        paddingBottom: theme.spacing.unit * 2, 
        width: 340,
        position: 'absolute',
        top: '20px',
        left: '20px'
      },    
    grow: {
      textAlign: 'left',
      paddingBottom: '20px'
    },
    paragraph: {
        width: '100%',
        textAlign: 'left',
        paddingBottom: '20px',
      },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        paddingBottom: '20px'
      },
    button: {
      margin: theme.spacing.unit,
      color: '#3f51b5',
      border: '1px solid rgba(63, 81, 181, 0.5)'
    },
    link : {
        textDecoration: 'none',
        color: '#3f51b5',
      }    
  });

class TaxiOrder extends PureComponent {

    state={
        address:{
            from: '',
            to: ''
        },
        addressList: [],      
    }

    componentDidMount(){
        const { savedData } = this.props
        if(savedData!== []){           
            getAddressList().then(jsonData => {
                this.setState({ 
                    addressList: jsonData.addresses
                })                                
            })  
        }
    }       

    handleChange = direction  => event => {
        const { address } = this.state
        this.setState({
            address: { ...address, [direction]: event.target.value }           
        });
      };

    handleReset = () =>{
        const {resetTaxiOrder} = this.props 
        resetTaxiOrder()
        this.setState({
            address: { from: '', to: '' }           
        });
    } 

    handleSubmit = () =>{
        const { address } = this.state
        const {createTaxiOrder} = this.props 
        this.setState({
            order: true          
        });       
        createTaxiOrder({from: address.from, to: address.to})
        
    } 

    addressFilter(value, direction) {
        const { address} = this.state
        if (value !== address[direction]){
            return(
            <MenuItem key={value} value={value}>
              {value}
            </MenuItem>
            )
        }
      }

    render() {            
        const { savedData } = this.props
        const { classes, isOrder } = this.props;  
        return (
            <div className={classes.root}>
                <Map/>
                {
                    savedData.length !== 0 
                    ? (
                        isOrder
                        ? this.renderResetForm()
                        : this.renderOrderForm()
                        )
                    
                    : this.renderProfileLink()
                }
            </div>
            
    )}

    renderResetForm = () => {
        const { classes } = this.props;  
        return (
            <Paper className={classes.container}>
                <Typography variant="h4" color="inherit" className={classes.grow}>
                    Order placed
                </Typography>
                <Typography className={classes.paragraph} component="p">
                    Your taxi is already coming to you. Will arrive in about 10 minutes.
                </Typography>
                <Button 
                    className={classes.button} 
                    onClick={this.handleReset}
                    >
                    Make a new order
                </Button>
            </Paper>
        )
    }
    
    renderProfileLink = () => {
        const { classes } = this.props;          
        return (
            <Paper className={classes.container}>
                <Typography variant="h5" color="inherit" className={classes.grow}>
                    To make an order please fill in your payment details in Profile
                </Typography>
                <Typography className={classes.paragraph} component="p">
                    Enter your bank card information to make an order
                </Typography>
                <Button className={classes.button} >
                    <Link className={classes.link} to='/profile'>
                        Go to Profile
                    </Link>
                </Button>
            </Paper>
        )
    }

    renderOrderForm = () => {
        const { classes } = this.props;  
        const {address, addressList} = this.state
        return (
            <Paper className={classes.container}>
            <form className={classes.form_container} noValidate autoComplete="off">
                <Typography variant="h4" color="inherit" className={classes.grow}>
                    Taxi order
                </Typography>
                <CastomTooltip title="try password '123123'" placement="right-end">
                    <TextField
                    id="addressFrom"
                    select                  
                    name="from"
                    value={address.from}
                    label="Select departure address"
                    onChange={this.handleChange('from')}
                    className={classNames(classes.margin, classes.textField)}                    
                >
                   {   addressList.length !== 0
                        //? addressList.filter(this.addressFilter(address.from))
                        ? addressList.map(option => {
                            if(address.to !== option)
                            return (
                            <MenuItem key={option} value={option}>
                              {option}
                            </MenuItem>)
                            return null
                          })
                        : null}                    
                    </TextField>
                </CastomTooltip>
                <TextField
                    id="addressTo"
                    select          
                    name="to"
                    value={address.to}
                    label="Select arrival address"
                    onChange={this.handleChange('to')}
                    className={classNames(classes.margin, classes.textField)}                    
                >
                    {   addressList.length !== 0
                        //? addressList.filter(this.addressFilter(address.from))
                        ? addressList.map(option => {
                            if(address.from !== option)
                            return (
                            <MenuItem key={option} value={option}>
                              {option}
                            </MenuItem>)
                            return null
                          })
                        : null} 
                    
                </TextField>
                {
                address.to !== '' && address.from !== ''
                ? 
                <Button 
                    className={classes.button} 
                    color="inherit"
                    onClick={this.handleSubmit}
                    >
                    Order taxi
                </Button>
                : 
                <Button disabled className={classes.button}>
                    Order taxi
                </Button>
                }
            </form>
            </Paper>
        )
    }

}

export default withLocalstorage('profile-data', [])(connect(state => (
    { 
      isOrder: getIsOrder(state),
    }),
    {
    createTaxiOrder, 
    resetTaxiOrder
    }
)(withStyles(styles)(TaxiOrder)))
